import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { pink } from '@material-ui/core/colors';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Divider from '@material-ui/core/Divider';
import Terrain from '@material-ui/icons/Terrain';
import { Typography } from '@mui/material';

const data = [
    { title: 'Заря далее 5 улицы', add: '+3p' },
    { title: 'Мичуринец', add: '+3p' },
    { title: 'Строитель', add: '+3p' },
    { title: 'Березка', add: '+3p' },
    { title: 'Изобилие', add: '+3p' },
    { title: 'Автомобилист', add: '+3p' },
    { title: 'Заброденье (кроме Утро)', add: '+3p' },
    { title: 'Родничок', add: '+3p' },
    { title: 'Старые грядки', add: '+3p' },
    { title: 'Лавля', add: '+3p' },
    { title: 'Сухой остров', add: '+3p' },
    { title: '"Барсуковское" озеро', add: '+3p' },
    { title: 'Яловка (кроме проезда через Барсуки)', add: '+3p' },
    { title: 'Восход грядки', add: '+3p' },
    { title: 'Радуга-4', add: '+3p' },
    { title: 'Минута', add: '+3p' },
    { title: 'Лесная поляна', add: '+3p' },
    { title: 'Бытовик', add: '+3p' },
    { title: 'Сутоки', add: '+3p' },
]

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        maxWidth: '100%',
        marginTop: 55,
        backgroundColor: theme.palette.background.paper,
    },
    pink: {
        color: theme.palette.getContrastText(pink[500]),
        backgroundColor: pink[500],
    },
}));

const AppContent = () => {
    const classes = useStyles();
    return (
        <List className={classes.root}>
            {data.map((item, key) => {
                return (
                    <>
                        <ListItem key={key}>
                            <ListItemAvatar>
                                <Avatar className={classes.pink}>
                                    <Terrain />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText primary={
                                <Typography variant='h5' className={classes.text}>{item.title}</Typography>
                            } />
                        </ListItem>
                        <Divider />
                    </>
                )
            })}
        </List>
    )
}
export default AppContent;